





































































































import Vue from "vue";
import Component from "vue-class-component";
import Swipe from "../../components/Swipe.vue";
import mixin from "../../config/mixin";
import { Action } from "vuex-class";

@Component({
  components: {
    Swipe
  },
  mixins: [mixin]
})
export default class User extends Vue {
  @Action("setTabIndex") setTabIndex;
  // ORDER_WAIT_PAY
  // ORDER_CANCEL_PAY
  // ORDER_WAIT_SENDGOODS
  // ORDER_WAIT_RECVGOODS
  // ORDER_WAIT_REVIEW
  // ORDER_END_GOODS
  // ORDER_FINISH
  userIcon="";
  orderList = [
    {
      name: "待付款",
      url: "",
      status: "ORDER_WAIT_PAY"
    },
    {
      name: "待发货",
      url: "",
      status: "ORDER_WAIT_SENDGOODS"
    },
    {
      name: "待收货",
      url: "",
      status: "ORDER_WAIT_RECVGOODS"
    },
    {
      name: "待评价",
      url: "",
      status: "ORDER_WAIT_REVIEW"
    },
    {
      name: "退换/售后",
      url: "",
      status: "REFUND"
    }
  ];
  toolsList = [
    {
      name: "我的收藏",
      url: ""
    },
    {
      name: "地址管理",
      url: ""
    },
    {
      name: "优惠券",
      url: ""
    },
    {
      name: "我的成员",
      url: ""
    }
  ];
  getOrderList(status) {
    this.$router.push({
      name: "orderlist",
      query: {
        orderStatus: status
      }
    });
  }
  queryuserinfo(){
    Vue.prototype.$reqFormPost(
      "/user/query",
      {
        userId: this.$store.getters[
          Vue.prototype.MutationTreeType.TOKEN_INFO
        ].userId,
        token: this.$store.getters[
          Vue.prototype.MutationTreeType.TOKEN_INFO
        ].token,
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          return;
        }
        
        this.userIcon = res.data.data.userIcon;
        console.log('userIcon',res.data.data.userIcon);
      }
    );
  }
  tools(n) {
    if (n.name == '我的收藏') {
      this.$router.push({ name: "collection" });
    }
    if (n.name == '地址管理') {
      this.$router.push({ name: "addresslist" });
    }
    if (n.name == '优惠券') {
      this.$router.push({ name: "coupon" });
    }
    if (n.name == '我的成员') {
      this.$router.push({ name: "my_member" });
    }
  }
  go_setting(){
    this.$router.push("/setting");
  }
  go_essential(){
    this.$router.push("/essential_information");    
  }
  mybankcard(){ 
    this.$router.push("/my_bankcard");
  }
  myreward(){
    this.$router.push("/my_reward");
  }
  mounted() {
    this.setTabIndex(3);
    if(this.$store.getters[
          Vue.prototype.MutationTreeType.TOKEN_INFO
        ].userId != '' && this.$store.getters[
          Vue.prototype.MutationTreeType.TOKEN_INFO
        ].token!= ''){
    this.queryuserinfo();
        }
    console.log("个人中心加载");
  }
}



