


























































import Vue from "vue";
import Component from "vue-class-component";
import Swipe from "../../components/Swipe.vue";
import mixin from "../../config/mixin";
import comhead from "../../components/Comhead.vue";
import { List } from "vant";
@Component({
  components: {
    Swipe,
    comhead
  },
  mixins: [mixin]
})
export default class ProductList extends Vue {
  secCategoryItem = {};
  list = [];
  loading = false;
  finished = false;
  selectIndex = "";
  catlist = "";
  goodsList = [];
  catId = "";
  parentCatId = "";
  pageIndex = 20;
  title = "";
  goProductDetail(goodsId) {
    this.$router.push({
      path: "/productdetail",
      query: {
        goodsId: goodsId
      }
    });
  }
  selectCatItem(index) {
    let item = this.catlist[index];
    if (item["catId"] != this.catId) {
      this.goodsList = [];
      this.pageIndex = 20;
    }
    this.catId = item["catId"];
    this.selectIndex = index;
    this.getProductList();
    this.$router.replace({
      path: "/productlist",
      query: {
        catId: this.catId,
        parentCatId: this.parentCatId,
        selectIndex: index,
        title: this.title
      }
    });
  }
  loadMore() {
    this.loading = true;
    let self = this;
    setTimeout(() => {
      this.pageIndex += 20;
      this.getProductList();
    }, 1000);
  }
  getProductList() {
    Vue.prototype.$reqFormPost(
      "/user/goods/list",
      {
        catId: this.catId,
        page: 0,
        pageSize: this.pageIndex
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          return;
        }

        if (this.loading) {
          this.goodsList = res.data.data.goodsList;
          if (res.data.data.goodsList.length != this.pageIndex) {
            this.loading = false;
          }
        }
      }
    );
  }

  getCategoryList() {
    Vue.prototype.$reqFormPost(
      "/user/cat/list",
      {
        parentId: this.parentCatId
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          return;
        }
        this.catlist = res.data.data;
        console.log("getCategoryList", res.data.data);
      }
    );
  }

  mounted() {
    //load products
    this.catId = this.$route.query.catId;
    this.getProductList();

    //load categories
    this.parentCatId = this.$route.query.parentCatId;
    this.getCategoryList();

    //load selectIndex
    this.selectIndex = this.$route.query.selectIndex;

    //load title
    this.title = this.$route.query.title;
  }

  handlePX(CssName, PxNumber) {
    return (
      CssName +
      ":" +
      this.$store.getters[Vue.prototype.MutationTreeType.SYSTEM].availWidth /
        750 *
        PxNumber +
      "px;"
    );
  }
}



